* {
    box-sizing: border-box;
  }
  
.App-link {
    color: #61dafb;
}

.square {
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    width: 100%;
    padding-bottom: 30%;
    margin: 1.66%;
    background-color: #ffffff;
    color: #1E1E1E;
    text-align: justify;
    overflow-y: scroll;
    height: 350px;
    border: 1px solid #1E1E1E;
}

.square-01{
  font-family: 'Montserrat', sans-serif;
